.grid-images{
    margin: 5px;
	display: grid;
	grid-template: repeat(3,33vw) / repeat(3,30vw);
    -webkit-justify-content: center;
            justify-content: center;
	grid-gap: 10px;
    grid-auto-rows: 33vw;
}

.box{
    background: #f0050f;
}

#addEmotion{
    position: fixed;
    border-radius: 50%;
    background-color: #ffffff;
    border: 1px solid #5281b7;
    height: 52px;
    width: 52px;
    z-index: 100;
    top: 80vh;
    left: 80vw;
    text-align: center;
    background-image:'https://image.flaticon.com/icons/svg/109/109526.svg';
}

.float{
	position:fixed;
	width:60px;
	height:60px;
	bottom:40px;
	right:40px;
	background-color:#0C9;
	color:#FFF;
	border-radius:50px;
	text-align:center;
	box-shadow: 2px 2px 3px #999;
}

.my-float{
	margin-top:22px;
}

